.Nav{
    display: flex;
    width: 100vw;
    height: 100vh;
    justify-content: center;
}
.main_search_bar{
    text-align: center;
    display: flex;
    flex-direction: column;
}
.input_degin{
    display: flex;
    border: 1px solid rgb(204, 204, 204);
    padding: 15px;
    border-radius: 25px;
}
.icon_img{
    width: 22px;
    height: 22px;
}
.icon_img > img{
    width: 100%;
    height: 100%;
}

.inputn{
    width: 350px;
    height: 20px;
    font-size: 15px;
    outline: none;
    border: none;
}
.title{
    cursor: pointer;
    transition: all .5s;
}
.title:hover{
    color:rgb(128, 122, 122);
}

@media (max-width:765px) {
    .inputn{
        width: 200px;
    }
}