*{
    text-align: none;
}


.content{
    margin: auto;
    width: 1200px;
    height: 800px;
    background-color: bisque;
    overflow-y: scroll;
}
.marfin{
    flex-direction: column;
    width: 100vw;
    height: 100vh;
    display: flex;
    align-items: center;
}
.item{
    cursor: pointer;
    text-align: center;
    background-color: rgb(240, 235, 235);
    border-radius: 5px;
}
.C_content > ul{
list-style: none;
}
.lol {
    text-align: center;
    width: 200px;
}
.lol h3,p{
    text-align: center;
    margin: 0;
    padding: 0;
}
.df{
    justify-content: flex-start;
    display: flex;
    gap: 15px;
    flex-wrap: wrap;
}

.item > img {
    width: 200px;
    height: 200px;
}

@media (max-width:765px) {
    .marfin{
       text-align: center;
       height: auto;
    }
    .title{
        text-align: center;
    }
    .content{
        width: 390px;
        justify-content: center;
    }
    .df{
        justify-content: center;
        padding: 0;
    }
}